<template>
  <div id="app">
    <Header img="" school='Go语言实战抽奖系统'></Header>
    <div class="container-fluid">
      <hr style="border: none">
      <div class="row" style="margin-bottom:40px;">
        <div class="col-2 col-md-offset-1 navigation">
          <div class="row">
            <!--<div class="col-12 text-center">-->
            <!--<img :src="form.userPhotoUrl" width="103px" height="103px" style="margin-top: 23px;border-radius: 50%" @click="$refs.headPortrait.click()" @mouseover="changeEditImgStatus('img')" @mouseout="changeEditImgStatus('img')" :class="editImgFlg?'img-focus':''">-->
            <!--<input style="display:none" type="file" name="fileUpload" @change="uploadPhoto($event)" ref="headPortrait" >-->
            <!--</div>-->
            <div class="col-md-12 text-center">
              <div class="rank-img mt-4" :style="{backgroundImage:'url('+ form.userPhotoUrl + ')'}">
                <div class="rank-user" @click="$refs.headPortrait.click()">修改头像</div>
                <input style="display:none" type="file" name="fileUpload" @change="uploadPhoto($event)" ref="headPortrait" accept='image/*'>

              </div>
            </div>
            <span class="edit-img" v-if="editImgFlg" @mouseover="changeEditImgStatus('span')" @mouseout="changeEditImgStatus('span')" >修改头像</span>
            <div class="col-12 text-center" style="height: 50px;margin-top: 25px" v-if="!nickNameEditFlg">
              <span v-if="form.userNickname === null" class="nick-name" >请设置昵称</span>
              <span v-else class="nick-name">{{form.userNickname}}</span>
              <a class="edit" @click="changeNickNameFlg">修改</a>
            </div>
            <div class="col-12 text-center" style="height: 50px;margin-top: 25px" v-if="nickNameEditFlg">
              <div class="col-md-12 text-center"><input maxlength="6" type="text" class="nick-name el-input__inner" v-model="nickNameEdit" style="width: 80px!important;height:25px!important;"/></div>
              <div class="col-md-12 text-center"><a class="edit" style="margin-left: 0px" @click="saveNickName()">保存</a><a class="edit" @click="changeNickNameFlg" style="color: rgba(153,153,153,1)!important">取消</a></div>
            </div>
          </div>
          <div class="row" style="border-top:1px solid #EBEBEB">
            <div class=" text-center" style="height: 56px;line-height: 56px;background:rgba(248,248,248,1);">
              <div style="height: 33px;width: 4px;background:#6612CF;float: left;margin-top: 10px;border-radius: 2px"></div><span>个人资料</span>
            </div>
            <div class="col-12 text-center" style="height: 56px;line-height: 56px">
              <a href="personal-management.html" style="color: #333333">账户管理</a>
            </div>
          </div>
        </div>
        <div class="col-8  person-detail">
          <div style="border-bottom: 1px solid #EBEBEB;padding:unset;height: 56px;line-height: 56px;">
            <span class="detail-title">个人资料</span>
          </div>
          <div class="row" style="padding-left: 38px;margin-top: 20px">
            <h4 class="ml-5 xinxi-title">基本信息</h4>
            <el-form ref="form" :model="form" :rules="rules" label-width="80px">
              <div class="row mr-3 mt-5">
                <div class="col-6">
                  <el-form-item label="姓名" prop="userRealname">
                    <el-input placeholder="请输入姓名" v-model="form.userRealname"></el-input>
                  </el-form-item>
                  <el-form-item label="性别" prop="userSex">
                    <el-radio v-model="form.userSex" label="0">男</el-radio>
                    <el-radio v-model="form.userSex" label="1">女</el-radio>
                  </el-form-item>

                </div>
              </div>
              <h4 class="ml-5 xinxi-title">校园信息</h4>
              <div class="row mt-5 mr-3">
                <div class="col-6">
                  <el-form-item label="邮箱" prop="userEmail">
                    <el-input placeholder="请输入邮箱地址" v-model="form.userEmail"></el-input>
                  </el-form-item>
                </div>
                <div class="col-6">
                  <el-form-item label="学校">
                    <el-input v-model="form.schoolName" :disabled="true"></el-input>
                  </el-form-item>
                </div>
              </div>
              <div class="row mr-3">
                <div class="col-6">
                  <el-form-item label="学号" prop="userStudentNumber">
                    <el-input placeholder="请输入学号" v-model="form.userStudentNumber" maxlength="16"></el-input>
                  </el-form-item>
                </div>
                <div class="col-6">
                  <el-form-item label="学院/系">
                    <el-input v-model="form.facultyName" :disabled="true"></el-input>
                  </el-form-item>
                </div>
              </div>
              <div class="row mr-3">
                <div class="col-6">
                  <el-form-item label="QQ" prop="qq">
                    <el-input placeholder="请输入QQ" v-model="form.qq"></el-input>
                  </el-form-item>
                </div>
                <div class="col-6">
                  <el-form-item label="专业">
                    <el-input v-model="form.majorName" :disabled="true"></el-input>
                  </el-form-item>
                </div>
              </div>
              <div class="row mr-3">
                <div class="col-6">
                  <el-form-item label="入学时间"  prop="userClassId">
                    <el-select v-model="form.userEnterYear" placeholder="请选择入学时间" @change="changeEnterYear">
                      <el-option
                        v-for="item in enterYearData"
                        :key="item"
                        :label="item"
                        :value="item">
                      </el-option>
                    </el-select>
                  </el-form-item>
                </div>
                <div class="col-6">
                  <el-form-item label="班级" prop="userClassId">
                    <el-select v-model="form.userClassId" placeholder="请选择班级">
                      <el-option
                        v-for="item in classData"
                        :key="item.classId"
                        :label="item.className"
                        :value="item.classId">
                      </el-option>
                    </el-select>
                  </el-form-item>
                </div>
                <!--                <a href="" style="float: left;padding-top: 10px;color: #6612CF;font-family: PingFang-SC-Regular;font-size: 12px;">申请调整</a>-->
              </div>
              <div class="row mt-4">
                <a @click="save('form')" class="center-block btn save-btn" style="padding-top: 12px;color: white;">保存</a>
              </div>
            </el-form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../../../components/assistant/Header.vue'
export default {
  name: 'app',
  mounted () {
    this.loadData()
  },
  components: {
    Header
  },
  methods: {
    loadData () {
      let me = this
      this.$http.post('/user/queryuserinfo', {}, res => {
        // console.log(res.data)
        if (res.success) {
          $.extend(me.form, res.data)
          if (res.data.userEnterYear !== null) {
            me.$http.post('/user/queryclass', { 'userEnterYear': res.data.userEnterYear }, res => {
              // console.log(res.data)
              me.classData = res.data
            })
          }
        }
      })
      let enterYearData = []
      let nowYear = new Date().getFullYear()
      for (let i = 0; i < 10; i++) {
        enterYearData.push(nowYear - i)
      }
      this.enterYearData = enterYearData
    },
    changeEnterYear () {
      let me = this
      me.form.userClassId = ''
      this.$http.post('/user/queryclass', {
        userEnterYear: this.form.userEnterYear
      }, res => {
        if (res.success) {
          me.classData = res.data
        }
      })
    },
    changeNickNameFlg () {
      this.nickNameEditFlg = !this.nickNameEditFlg
    },
    saveNickName () {
      let me = this
      if (me.nickNameEdit === '') {
        this.$alert('修改昵称不能为空！', '提示', {
          confirmButtonText: '确定'
        })
        return false
      }
      me.$http.post('/user/modifynickname', { 'token': localStorage.getItem('token'), 'userNickname': me.nickNameEdit }, res => {
        if (res.success) {
          var formData = new FormData()
          formData.append('snsNo', localStorage.getItem('snsNo'))
          formData.append('nickName', me.nickNameEdit)
          me.$http.snspost('/member/lyingEditMemberNkNm', formData, ressns => {
            me.$alert('保存成功！', '提示', {
              confirmButtonText: '确定'
            })
            me.nickNameEditFlg = false
            me.form.userNickname = me.nickNameEdit
          })
        }
      })
    },
    changeEditImgStatus (type) {
      if (type === 'img') {
        this.editImgFlg = !this.editImgFlg
      } else {
        this.editImgFlg = true
      }
    },
    uploadPhoto (event) {
      // console.log(1)
      let me = this
      let file = event.target.files[0]
      let imgSize = file.size
      if (imgSize >= 3 * 1024 * 1024) {
        me.$alert('图片大小不能超过3MB！', '提示', {
          confirmButtonText: '确定'
        })
        return false
      }
      me.valWidthAndHeight(file).then(function (valid) {
        if (valid) {
          let formData = new FormData()
          formData.append('file', file)
          me.$http.uploadFile('/user/uploadimg', formData, res => {
            // console.log(res)
            let avatar = res.data
            if (res.success) {
              var formData = new FormData()
              formData.append('snsNo', localStorage.getItem('snsNo'))
              formData.append('avatar', avatar)
              me.$http.snspost('/member/lyinguploadavatar', formData, ressns => {
                // console.log(ressns)
                me.form.userPhotoUrl = avatar
                me.$alert('上传成功！', '提示', {
                  confirmButtonText: '确定'
                })
              })
            } else {
              // 返回错误的处理
            }
          })
        }
      })
    },
    // 验证图片宽高
    valWidthAndHeight: function (file) {
      return new Promise(function (resolve, reject) {
        let _URL = window.URL || window.webkitURL
        let image = new Image()
        image.src = _URL.createObjectURL(file)
        image.onload = function () {
          let valid = image.width / image.height === 1
          valid ? resolve() : reject()
        }
      }).then(
        () => {
          return true
        },
        () => {
          this.$alert('上传图片尺寸不符合,只能是1:1!', '提示', {
            confirmButtonText: '确定'
          })
          return false
        }
      )
    },
    save (formName) {
      let me = this
      if (me.form.userNickname === '' || me.form.userNickname == null || me.form.userNickname === undefined) {
        me.$alert('请先设置昵称!', '提示', {
          confirmButtonText: '确定'
        })
        return false
      }
      this.$refs[formName].validate((valid) => {
        if (valid) {
          me.$http.post('/user/modifybasicinfo', me.form, res => {
            if (res.success) {
              let sex = '男'
              if (me.form.userSex === '1') {
                sex = '女'
              }
              var formData = new FormData()
              formData.append('id', localStorage.getItem('snsNo'))
              formData.append('name', me.form.userRealname)
              formData.append('sex', sex)
              formData.append('introduce', '')
              me.$http.snspost('/member/editLyingBaseInfo', formData, res => {
                me.$alert('保存成功！', '提示', {
                  confirmButtonText: '确定'
                })
              })
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  },
  data () {
    var filterQq = (rule, value, callback) => {
      this.form.qq = value.replace(/[^\d]/g, '')
      callback()
    }
    var filterUserStudentNumber = (rule, value, callback) => {
      if (value === '' || value === null) {
        callback()
      }
      this.form.userStudentNumber = value.replace(/[^\d]/g, '')
      callback()
    }
    var filterRealName = (rule, value, callback) => {
      if (value === '' || value === null) {
        callback(new Error('请输入姓名'))
      }
      var reg = /^[\u4E00-\u9FA5\uf900-\ufa2d·s]{2,20}$/// 验证姓名正则
      if (reg.test(value)) {
        callback()
      } else {
        callback(new Error('姓名格式不正确'))
      }
    }
    return {
      avatar: 'https://gss0.bdstatic.com/-4o3dSag_xI4khGkpoWK1HF6hhy/baike/c0%3Dbaike92%2C5%2C5%2C92%2C30/sign=62d46c39067b020818c437b303b099b6/d4628535e5dde7119c3d076aabefce1b9c1661ba.jpg',
      classData: [],
      enterYearData: [],
      editImgFlg: false, // 修改头像提示
      nickNameEditFlg: false, // 修改昵称控制区域显示
      nickNameEdit: '', // 修改的昵称
      form: {
        userNickname: '',
        userSex: '男',
        userEmail: '',
        userRealname: '',
        userStudentNumber: '',
        schoolName: '',
        facultyName: '',
        majorName: '',
        userClassId: '',
        userEnterYear: '',
        userPhotoUrl: '',
        qq: ''
      },
      validWidth: 1024,
      validHeight: 1024,
      rules: {
        userRealname: [
          { validator: filterRealName, trigger: 'blur' }
        ],
        userSex: [
          { required: true, message: '请选择性别', trigger: 'blur' }
        ],
        userEmail: [
          { type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur' },
          { min: 5, max: 20, message: '邮箱格式不正确', trigger: 'blur' }
        ],
        username: [
          { required: true, message: '请输入姓名', trigger: 'blur' }
        ],
        userClassId: [
          { required: true, message: '请选择班级', trigger: 'blur' }
        ],
        userStudentNumber: [
          { validator: filterUserStudentNumber, trigger: 'change' }
        ],
        qq: [
          { required: true, message: '请输入QQ号', trigger: 'blur' },
          { min: 5, max: 11, message: 'QQ号格式不正确', trigger: 'blur' },
          { validator: filterQq, trigger: 'change' }
        ]
      }
    }
  }
}
</script>

