    box-shadow:0px 4px 15px 0px rgba(102, 207, 18, 0.35) !important;
    border-radius:23px!important;
    margin-bottom: 20px;
  }
  .rank-img{
    border-radius: 50%;
    width:100px;
    height:100px;
    margin: 0px auto;
    background-size: contain;
    .rank-user{
      color:rgba(255,255,255,1);
      width:100%;
      height:100%;
      background-color: #000000;
      border-radius:50%;
      opacity:0.68;
      display:none;
      z-index:101;
      font-family:PingFang-SC-Regular;
      font-weight:400;
      padding-top: 42px;
      cursor:pointer;
    }
  }
  .rank-img:hover{
    .rank-user{
      display: block;
    }
  }
</style>
